import React from 'react';
import { Textarea } from '@heroui/react';

import CommonToolTip from '@/app/common-components/CommonToolTip';

interface CommonTextAreaProps {
  labelText?: string;
}

function CommonTextArea({ labelText }: CommonTextAreaProps) {
  const label = (
    <div className='flex items-center gap-1 text-left'>
      <span>{labelText}</span>
      <CommonToolTip content='Craft 2–4 punchy sentences that capture attention! Highlight your role, experience, and most importantly — showcase your top achievements, standout qualities, and key skills.' />
    </div>
  );

  return (
    <div className='common-textarea__component'>
      {labelText && (
        <div className='mb-2'>
          {label}
        </div>
      )}
      <Textarea
        disableAnimation
        disableAutosize
        classNames={{
          base: 'w-full',
          input: 'resize-y min-h-[100px]',
        }}
        placeholder='Enter your description'
      />
    </div>
  );
}

export default CommonTextArea;
